import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const BotschafterWerner = () => {
  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "2020_0010_Goetz.png" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid_noBase64
          }
        }
      }
    }
  `)

  return <Img fluid={data.placeholderImage.childImageSharp.fluid} alt="Prof. Gtz W. Werner, 'Frher oder spter werden wir einsehen, dass an einem Einkommen fr alle kein Weg vorbei fhrt. Je frher, desto besser."/>
}

export default BotschafterWerner
